.app {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.channels {
  flex-shrink: 0;
  width: 250px;
}

.info {
  height: 55px;
  padding: 24px;
  background: rgb(41, 43, 47);
}

.info h4 {
  margin: 0 0 5px;
}

.chat {
  border-left: 2px solid rgb(41 43 47);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  position: relative;
}

.members {
  transition: 0.3s ease-in-out;
  flex-shrink: 0;
  width: 0;
  transform: translateX(310px);
}

.members.shown {
  transform: none;
  width: 310px;
}

.people {
  align-items: center;
  color: gray;
  cursor: pointer;
  display: flex;
  font-size: 13px;
  position: absolute;
  right: 34px;
  top: 34px;
  z-index: 1;
}

.people span {
  margin: 2px 8px 0 0;
}

.people.active {
  color: rgba(223, 38, 65, 0.8);
}

.pn-channel-list--dark {
  --channel--active__background: rgba(223, 38, 65, 0.8);
  --channel--hover__background: rgba(223, 38, 65, 0.6);
  --channel__description__display: none;
  --channel__membership__top: 25px;
  --channel__name__fontSize: 13px;
  --channel__name__fontWeight: 400;
  --channel__padding: 20px 24px;
}

.pn-msg-input--dark {
  --msg-input__padding: 0 24px 24px;
  --msg-input__send__background: rgb(223, 38, 65);
}
